/**
 * key
 * @method uniqueKey
 * 
 * @returns
 * {String}  
 * 
 * @author Yunior
 */
export const uniqueKey =()=>{
    return Math.random().toString(32).slice(2)
}


/**
 * arrpropval
 * @method arrayFindObject
 * 
 * @params
 * arr  {Array} 
 * prop {String} 
 * val  {String|Number} 
 * 
 * @returns
 * {Object} 
 * 
 * @author Yunior
 * */
export const arrayFindObject=(arr,prop,val)=>{
    let result;
    if(Object.prototype.toString.call(arr)!=='[object Array]'||arr.length==0) return result

    result=arr.find((item,index)=>{
        if(item[prop]===val){
            return true
        }
    })
    return result
}


/**
 * arrpropval
 * @method arrayRemoveObject
 * 
 * @params
 * arr  {Array} 
 * prop {String} 
 * val  {String|Number} 
 * 
 * @returns
 * {Boolean} 
 * 
 * @author Yunior
 * */
export const arrayRemoveObject=(arr,prop,val)=>{
    let index;
    if(Object.prototype.toString.call(arr)!=='[object Array]'||arr.length==0) return

    index=arr.findIndex((item,index)=>{
        if(item[prop]===val){
            return true
        }
    })
    
    if(index!=-1){
        arr.splice(index,1)
        return true
    }else{
        return false
    }
}


/**
 * ,timecallback
 * @method throttleFn
 * 
 * @params
 * delay {Number} 
 * callback   {Function} 
 * @returns
 * 
 * @author Yunior
 * */
export const throttleFn=(delay, callback)=>{
    let timeoutID;
    let lastExec = 0;

    function wrapper() {
        const self = this;
        const elapsed = Number(new Date()) - lastExec;
        const args = arguments;

        function exec() {
            lastExec = Number(new Date());
            callback.apply(self, args);
        }

        clearTimeout(timeoutID);

        if (elapsed > delay) {
            exec();
        } else {
            timeoutID = setTimeout(exec, delay - elapsed);
        }
    }

    return wrapper;
}




/**
 * 
 * @Object validataUtil
 * 
 * @author Yunior
 */
export const validateUtil = {
    isPhone(number){
        return /^1[3,4,5,6,7,8,9]\d{9}$/.test(number)
    },
    isEmpty(str){
        return !String(str||'').trim()
    },
    isUrlKey(str){
        return /^[a-zA-Z\d_]+$/.test(str)
    }
}




/**
 *  eg:2019-09-24 11:31:06
 * @method formatDateTime
 * 
 * @params
 * time  {Number}} 
 * 
 * @returns
 * {String} 
 * 
 * */
export const formatDateTime = time => {
    let date=new Date(time*1)
    let year = date.getFullYear(),
        month = date.getMonth() + 1,
        day = date.getDate(),
        hour = date.getHours(),
        minute = date.getMinutes(),
        second = date.getSeconds();

    let formatNumber = n => {
        n = n.toString()
        return n[1] ? n : '0' + n
    }
  
    return [year, month, day].map(formatNumber).join('-') + ' ' + [hour, minute, second].map(formatNumber).join(':')
}
  
 
  


