<template>
<div>
  <!--  -->
  <HeadInfo />

  <!--  -->
  <div class="query-wraper">
    <el-row class="maxwidth" type="flex" justify="space-between">
      <el-col :span="5">
        
        <el-select v-model="status" placeholder="" @change="search">
          <el-option
            label=""
            value="">
          </el-option>
          <el-option
            label=""
            value="unrelease">
          </el-option>
          <el-option
            label=""
            value="release">
          </el-option>
          <el-option
            label=""
            value="offline">
          </el-option>
          <el-option
            label=""
            value="overdue">
          </el-option>
        </el-select>
      </el-col>

      <el-col :span="5">
        UrlKey
        <el-input placeholder="urlKey" v-model.trim="urlKey" class="query-search" @keydown.enter.native="search">
        </el-input>
      </el-col> 

      <el-col :span="5">
        
        <el-input placeholder="" v-model.trim="name" class="query-search" @keydown.enter.native="search">
        </el-input>
      </el-col>
      <el-col :span="5">
        
        <el-input placeholder="" v-model.trim="title" class="query-search" @keydown.enter.native="search">
        </el-input>
      </el-col>

      <el-col :span="4" class="align-right"> 
        <el-button class="query-btn" type="primary"  @click.native="search"> </el-button>
        <el-button class="query-btn" type="warning"  @click.native="reset"> </el-button>
      </el-col>
    </el-row>
  </div>


  <div class="page-list maxwidth">  
    <div class="operate-row">      
      <el-button class="operate-btn" type="success"  @click.native="releaseTap"> </el-button>     
      <el-button class="operate-btn" type="primary" @click="cratePage"> </el-button>   
    </div>

    <!--  -->
    <el-table
      class="list-table"
      @selection-change="handleSelectionChange"
      :data="tableData"
      border>

      <el-table-column
        type="selection"
        :selectable="selectable"
        width="70">
      </el-table-column>

      <el-table-column
        prop="urlKey"
        label="urlKey"
        >
      </el-table-column>

      <el-table-column
        prop="name"
        label=""
        >
      </el-table-column>
      
      <el-table-column
        prop="title"
        label=""
      >
      </el-table-column>

      <el-table-column 
        label="" 
        width="100">
        <template slot-scope="scope">
        {{statusText(scope.row)}}
        </template>
      </el-table-column>

      <el-table-column
        label=""
        width="180">
        <template slot-scope="scope">
        {{formatDate(scope.row)}}
        </template>
      </el-table-column>

      <el-table-column
        label=""
        width="180">
        <template slot-scope="scope">
        {{scope.row.createDate|formatDateTime}}
        </template>
      </el-table-column>

      <el-table-column
        label=""
        width="100">        
          -        
      </el-table-column>

      <el-table-column label=""
        width="320">
        <template slot-scope="scope">
          <span class="operate-btn" @click="copyLink(scope.row)"></span>
          <span class="operate-btn" @click="pageEditor(scope.row)"></span>
          <span class="operate-btn" @click="previewPage(scope.row)"></span>
          <span class="operate-btn operate-delete" @click="deletePage(scope.row)"></span>
          <span class="operate-btn" v-if="scope.row.status=='release'" @click="updatePageStatus(scope.row, 'offline')"></span>
        </template>
      </el-table-column>
    </el-table>

    <div class="page-box" v-if="totalSize>0">
      <el-pagination 
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-size="pageSize"
        :pager-count="5"
        layout="total, sizes, prev, pager, next"
        :total="totalSize"
        :disabled="disabled"
        :page-count="pageCount"
        :page-sizes="pageSizes">
      </el-pagination>
    </div>
  </div>
</div>
</template>

<script>
import './pagelist.scss'
import HeadInfo from '@/components/HeadInfo'
import http from '@/http/'
import {formatDateTime} from '@/utility/'

export default {
  name: 'PageList',
  data() {
    return {
      tableData:[],
      status:'',
      name:'',
      title:'',
      urlKey:'',
      disabled:false,
      pageSize:10,
      pageSizes:[10, 20, 30, 40, 50, 100],
      pageCount: 1,
      totalSize:0,
      page:1,
      multipleSelection: []
    }
  },
  created(){
   this.search()
  },
  methods:{
    //
    search(flg){
      if(flg != 'flg') {
        this.page = 1
      }
      let status=this.status,
          pageSize=this.pageSize,
          page=this.page,
          title=this.title,
          urlKey=this.urlKey,
          name=this.name;

      http.getAllPage({status, name, pageSize, page, urlKey, title})
      .then(res=>{     
        this.tableData=res.data||[]
        this.totalSize=res.totalSize||0
        if(res.totalSize == 0) {
          this.disabled = true
        }else {
          this.disabled = false
        }
        this.pageCount = Math.ceil(this.totalSize/this.pageSize)     
      })
    },
    //
    reset(){
      this.status='release'
      this.name=''
      this.title=''
      this.urlKey=''
      this.search()
    },
    //
    cratePage(){
      window.open('#/PageCreate','_blank')
    },
    //
    editorPage(page){
      window.open(`#/PageEditor?urlKey=${page.urlKey}`,'_blank')
    },
    //
    deletePage(page){
      this.$confirm('','',{
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
      .then(()=>{
        http.deletePage({urlKey:page.urlKey})
        .then(res=>{         
          if(this.tableData.length == 1 && this.page != 1) {
            this.page = this.page - 1
          }
          this.$message({
            type: 'success',
            message:res.message
          })
          this.search('flg')
        })
      })
      .catch(()=>{
        console.log('')
      })
    },
    //
    previewPage(page){
      window.open(`#/PagePreview?urlKey=${page.urlKey}`,'_blank')
    },
    //
    updatePageStatus(page, status){
      if(this.tableData.length == 1 && this.page != 1) {
          this.page = this.page - 1
      }
      this.$confirm('','',{
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
      .then(()=>{
        http.updatePageStatus({urlKeys:[page.urlKey], status:'offline'})
        .then(res=>{         
          this.$message({
            type: 'success',
            message:res.message
          })
      
          this.search('flg')
        })      
      })
      .catch(()=>{
        console.log('')
      })
    },
    //
    statusText(page){
      let status=page.status,
      result='';
      switch(status){
        case 'unrelease':
          result='';
        break;

        case 'release':
          result='';
        break;

        case 'offline':
          result='';
        break;

        case 'overdue':
          result='';
        break;
      }
      return result
    },
    //
    formatDate(page){
      let result=''

      if(page.needExpire){
        result=formatDateTime(page.expireDate)
      }else{
        result=''
      }
      return result
    },
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //
    releaseTap(){
      if(this.multipleSelection.length > 0) {
        let urlKeys= [];
        this.multipleSelection.forEach(function(el) {
          urlKeys.push(el.urlKey)
        }, this);
        if(urlKeys.length == this.tableData.length && this.page != 1) {
          this.page = this.page - 1
        }
        http.updatePageStatus({urlKeys:urlKeys,status:'release'})
        .then(res=>{       
          this.$message({
            message: '',
            type: 'success'
          });
          this.search('flg')         
        })
      } else {
        this.$message({
          message: '~',
          type: 'warning'
        });
      }
    },
    // 
    selectable(row,index) {
      if(row.status === "offline" || row.status === "unrelease"){
          return true
      }else {
          return false
      }
    },
    handleSizeChange(val) {
      // console.log(` ${val} `);
      this.pageSize =  val*1
      this.page = 1
      this.search('flg')
    },
    handleCurrentChange(val) {
      // console.log(`: ${val}`);
      this.page = `${val}` - 0
      this.search('flg')
    },
    // 
    copyLink(data){
      let url = window.location.origin+window.location.pathname+"#/landpage?urlKey="+data.urlKey;
      let oInput = document.createElement('input');
      oInput.value = url;
      document.body.appendChild(oInput);
      oInput.select(); // ;
      console.log(oInput.value)
      document.execCommand("Copy"); // 
      this.$message({
        message: '',
        type: 'success'
      });
      oInput.remove()
    },
    // 
    pageEditor(data){
      window.open(`#/PageEditor?urlKey=${data.urlKey}`,'_blank')
    }
  },
  components:{
    HeadInfo,
  }
}
</script>



